<template>
  <div class="component py-4">
    <h5 class="component__heading pt-0 pt-lg-2 px-3 px-lg-5">Sambaza Stake</h5>
    <div class="row">
      <div class="col-lg-9 col-xl-7">
        <BetForm class="px-3 px-lg-5">
          <form @submit.prevent="handleSambazaForm()">
            <div class="form-group">
              <input type="number" class="form-control"
                    v-model="sambaza.phone"
                    required
                    id="sambazaPhone" aria-describedby="phoneHelp" placeholder="Enter the friend's phone number">
            </div>
            <div class="form-group">
              <input type="number" class="form-control"
                    v-model="sambaza.amount"
                    required
                    id="amount" aria-describedby="phoneHelp" placeholder="Enter the Amount">
            </div>
            <div class="form-group">
              <div class="help text-center" v-show="sambazaResponseData">
                <i v-if="sambazaResponseData"
                  v-for="(error, index) in sambazaResponseData" :key="index"
                  style="padding: 4px; color: lightgreen">
                  {{ Object.values(error)[0].length ? Object.values(error)[0][0]: Object.values(error)[0] }}
                </i>
              </div>
            </div>
            <div class="form-group d-flex justify-content-between">
              <BetButton type="submit" :disabled="!sambaza.amount || !sambaza.phone || isSubmitted">Sambaza Stake</BetButton>
              <BetButton @click.prevent="$router.push({name: 'profile'})">
                <i class="fa fa-arrow-left"></i>
                Back
              </BetButton>
            </div>
          </form>
        </BetForm>
      </div>
    </div>

  </div>
</template>

<script>
  import axios from 'axios'
  import {mapGetters} from 'vuex'
  import {SAMBAZA_URL} from '../../api_routes'
  import {AUTH_CONFIG} from '../../auth_config'
  import BetForm from '../Utilities/BetForm'
  import BetButton from '../Utilities/BetButton'

  export default {
    name: 'SambazaStake',
    components: {BetForm, BetButton},
    data () {
      return {
        sambaza: {
          phone: '',
          amount: ''
        },
        isSubmitted: false,
        sambazaResponseData: null
      }
    },
    computed: {
      ...mapGetters(['authUser'])
    },
    methods: {
      handleSambazaForm () {
        this.isSubmitted = true
        let postData = {
          amount: this.sambaza.amount,
          recipient: this.sambaza.phone,
          sender: this.authUser.name

        }
        axios.post(SAMBAZA_URL, postData, {headers: AUTH_CONFIG.getHeaders()}).then((response) => {
          this.isSubmitted = false
          if (response.status === 200) {
            this.sambazaResponseData = response.data
            this.$noty.show('Operation successful')
            this.$store.dispatch('init_auth_user', {request: 'refresh'})
            this.$router.push({name: 'profile'})
          }
        }, (error) => {
          this.isSubmitted = false
          this.sambazaResponseData = error.response.data
        })
      }
    }
  }
</script>

